/* eslint-disable camelcase */
/* eslint-disable no-shadow */
import { api, getSessionKey, getArgsFromUrl } from '@badam/common-utils'

const url =
  process.env.VUE_APP_ENV === 'development'
    ? 'https://test08-pk.badambiz.com'
    : 'https://dynamic7kiczp.zyrpox.buzz'

api.getAxiosInstance().defaults.baseURL = url
api.setWgetEnable(true)

export const session_key = getSessionKey() || 'testskey_2000026159'

export enum UserRecall202312StatusEnum {
  WAIT = 0,
  SENT = 1,
  SUCC = 2,
}

interface RecallUser {
  uid: number
  display_uid: number
  icon: string
  nickname: string
  status: UserRecall202312StatusEnum
}

interface listRes {
  query_token: string
  users: RecallUser[]
}

const headers = {
  'x-cli-tk': session_key,
}

export function userList(is_recalled: boolean, query_token = '') {
  return api.wget<listRes>(
    '/api/user_recall202410/list/',
    {
      is_recalled,
      query_token,
      size: 20,
    },
    headers
  )
}

export function sendReCall(target_uids: number[]) {
  return api.wget<any>(
    '/api/user_recall202410/send_recall/',
    {
      target_uids,
      session_key,
    },
    headers
  )
}

export function receiveShareReward() {
  return api.wget<{ task_num_list: number[] }>(
    '/api/user_recall202410/receive_share_reward/',
    {},
    headers
  )
}

export function taskReceiveReward() {
  return api.wget<{ task_num_list: number[] }>(
    '/api/user_recall202410/task_receive_reward/',
    {},
    headers
  )
}

export function receiveReward() {
  return api.wget<any>('/api/user_recall202410/receive_reward/', {}, headers)
}

export function report(type: number) {
  return api.wget<any>(
    '/api/user_recall202410/data_reporting/',
    {
      type,
    },
    headers
  )
}

export function getVersionConfig(name: string) {
  return api.wget<any>('/api/sys/config/', {
    channel: name,
    pure: 1,
  })
}

export function thanks() {
  return api.wget<any>('/api/user_recall202410/thanks/', {}, headers)
}

export function userInvitors(query_token: string) {
  return api.wget<listRes>(
    '/api/user_recall202410/user_invitors/',
    {
      query_token,
    },
    headers
  )
}

export function getShare() {
  return api.wget<{
    share_info: {
      link: string
    }
  }>(
    '/api/room/get_share/',
    {
      name: 'user_recall_202410',
      session_key,
    },
    headers
  )
}

export function getShareDetail(info: string) {
  return api.get<{
    avatar: string
    nickname: string
    display_uid: string
  }>(
    '/api/room/get_share_detail/',
    {
      info,
      session_key,
    },
    {
      'x-cli-pn': 'com.sawa.web.ksa',
    }
  )
}

interface MyInfoRes {
  uid: number
  nickname: string
  icon: string
}

export enum GiftWallTaskStatusEnum {
  UNFINISH = 1,
  RECEIVEABLE = 2,
  RECEIVED = 3,
}

export function getMyUserInfo() {
  return api.wget<MyInfoRes>('/api/user/get_my_info/', {
    session_key,
  })
}

export type Task = { task_num: number; status: GiftWallTaskStatusEnum }

export type Info = {
  aim_info: { reward_status: GiftWallTaskStatusEnum }
  recall_send_num: number
  recall_succ_num: number
  task_reward_items: Task[]
  left_share_reward_num: number
  can_receive_share_reward_num: number
  can_recall_friend_num: number
  is_subcribe_notify: boolean
  is_hlcj_user: boolean
}

export function info() {
  return api.wget<{ info: Info; start_ts: number; end_ts: number }>(
    '/api/user_recall202410/info/',
    {},
    headers
  )
}

export interface RecallUserInviter {
  uid: number
  display_uid: number
  icon: string
  nickname: string
  is_thanks: boolean
  selected?: boolean
}
export interface GetUserInvitorsRsp {
  query_token: string
  users: RecallUserInviter[]
}

// 获取邀请我的人的列表
export function getUserInviters(query_token: string) {
  return api.wget<GetUserInvitorsRsp>(
    '/api/user_recall202410/user_invitors/',
    { query_token },
    headers
  )
}

// 给邀请我的人发送感谢
export function sendThanksToInvitors(target_uids: string[]) {
  return api.wget<any>('/api/user_recall202410/thanks/', { target_uids }, headers)
}

// 订阅召回列表新增通知
export function subscribe(is_cancel: boolean) {
  return api.wget<any>('/api/user_recall202410/subscribe_list_add_notify/', { is_cancel }, headers)
}

export function setApiBaseUrl(url: string) {
  if (url && process.env.VUE_APP_ENV === 'production') {
    api.getAxiosInstance().defaults.baseURL = url
  }
}
