import store from '@/store/index'
import { showToast } from '@badam/vue-components'
import { customAlphabet } from 'nanoid'
import { getSysTs, _T, fixDomain } from '@badam/common-utils'

export function getMp4url(url: string) {
  const DOMAIN = window.location.hostname
  if (DOMAIN.indexOf('cdn-') === -1) {
    return url.replace('cdn-', '')
  }
  return url
}

export function preloadVideo(source: string) {
  const URL = window.webkitURL || window.URL

  return new Promise((resolve, reject) => {
    const xhr = new XMLHttpRequest()
    xhr.open('GET', source, true)
    xhr.responseType = 'blob'

    xhr.onload = function () {
      if (xhr.status === 200 || xhr.status === 304) {
        const res = xhr.response
        if (/iphone|ipad|ipod/i.test(navigator.userAgent)) {
          const fileReader = new FileReader()
          fileReader.onloadend = function () {
            const resultStr = fileReader.result! as string
            resolve(resultStr)
          }
          fileReader.readAsDataURL(xhr.response)
        } else {
          resolve(URL.createObjectURL(res))
        }
      } else {
        reject(new Error(`http response invalid${xhr.status}`))
      }
    }

    xhr.send()
  })
}

export function isStart(needTip = true) {
  if (store.state.start && getSysTs() >= store.state.start) {
    return true
  }

  if (needTip) {
    showToast(_T('活动未开始'))
  }
  return false
}

export function isEnd(needTip = true) {
  if (store.state.end && getSysTs() >= store.state.end) {
    if (needTip) {
      showToast(_T('活动已结束'))
    }
    return true
  }
  return false
}

export function formatTime(time: number) {
  return time / 60
}

export function getUUid() {
  let uuid = ''
  if (localStorage.getItem('call-uuid2')) {
    uuid = localStorage.getItem('call-uuid2') || ''
  } else {
    uuid = String(customAlphabet('1234567890abcdef', 32)()) || ''
    localStorage.setItem('call-uuid2', uuid)
  }
  return uuid
}

export function sprintf2(expr: string, params: any) {
  if (!expr) {
    return ''
  }
  const ret = expr.replace(/\{([^}]*)*\}/g, (item) => {
    let key = item.slice(1)
    key = key.slice(0, key.length - 1)
    if (params[key] === 0) {
      return params[key]
    }
    return params[key] || ''
  })
  return ret
}

const domainMap: any = {
  'oss-pk-arab-zl.badambiz.com': 'oss-pk-arab-zl.badambiz.com',
  'oss-pk-arab.badambiz.com': 'oss-pk-arab.badambiz.com'
}

export function fixRankDomain(url: string) {
  if (url && url.startsWith('http')) {
    try {
      const u = new (window.URL || window.webkitURL)(url)
      const { host } = u
      const domain = domainMap[host]

      if (domain) {
        u.host = domain
      }

      return u.toString()
    } catch (err) {
      console.error(err)
      return url
    }
  }

  return url
}

export function updateDomain() {
  Object.keys(domainMap).forEach((key: string) => {
    const newUrl = fixDomain(`https://${key}/aaa`)
    const u = new (window.URL || window.webkitURL)(newUrl)
    const { host } = u

    domainMap[key] = host
  })
}
