const Modal = Vue.extend({
  data() {
    return {
      show: false,
      curComp: null as unknown as Component,
      params: {} as any,
      className: '',
      isTop: false,
      isIn: true,
      cancelable: true,
      useCloseBtn: true,
      top: 0,
      // eslint-disable-next-line @typescript-eslint/no-empty-function
      beforePressMask: () => {},
      // eslint-disable-next-line @typescript-eslint/no-empty-function
      beforePressClose: () => {},
    }
  },
  store,
  methods: {
    setBeforePressMask(cb?: () => any) {
      if (cb) {
        this.beforePressMask = cb
      }
    },
    onPressMaskBefore() {
      try {
        this.beforePressMask()
      } catch (e) {
        console.log(e)
      }
    },
    onPressMask(e: any) {
      setTimeout(() => {
        this.onPressMaskBefore()
        if (this.cancelable) {
          this.onCancel()
        } else {
          e.preventDefault()
        }
      }, 50)
    },

    setBeforePressClose(cb?: () => any) {
      if (cb) {
        this.beforePressClose = cb
      }
    },
    onPressCloseBefore() {
      try {
        this.beforePressClose()
      } catch (e) {
        console.log(e)
      }
    },
    onPressClose() {
      this.onPressCloseBefore()
      this.onClose()
    },
    onCancel() {
      this.onClose()
    },
    onClose() {
      this.isIn = false
      setTimeout(() => {
        this.show = false
      }, 100)
    },
    setBackBtn(isShow: boolean) {
      if (!(window as any).JavaBridgeObj) {
        return
      }
      ;(window as any).JavaBridgeObj.showBackBtn(
        JSON.stringify({
          bl: isShow,
        })
      )
    },
    showRefreshControllIfNeed: (isTop: any) => {
      if ((window as any).kinoJsApi && (window as any).kinoJsApi.setRefreshEnable) {
        ;(window as any).kinoJsApi.setRefreshEnable(isTop)
      }
      if ((window as any).JavaBridgeObj && (window as any).JavaBridgeObj.enableRefresh) {
        ;(window as any).JavaBridgeObj.enableRefresh(isTop)
      }
    },
  },
  watch: {
    show(n) {
      if (n) {
        // setRefreshEnable(false)
        store.state.modalCount += 1
        // this.setBackBtn(false)
        store.state.popupHide = false
        lockScreen()
        // const nn = lockScreen()
        // if (nn > 0) {
        //   this.top = nn
        // }
        // this.top = lockScreen()
        // setIsOpenModal(true)
      } else {
        // setRefreshEnable(true)
        store.state.modalCount -= 1
        store.state.popupHide = true
        if (store.state.modalCount === 0) {
          // this.setBackBtn(true)
          unlockScreen(this.top)
        }
        this.$emit('close')
        // setIsOpenModal(false)
      }
    },
  },
})

const showModal = (
  component: Component,
  params: any,
  cancelable = true,
  useCloseBtn = true,
  onPressMask?: () => any,
  onPressClose?: () => any
) => {
  const modal = new Modal({ el: document.createElement('div') })
  const el = modal.$el
  modal.curComp = component
  modal.params = params
  modal.cancelable = cancelable
  modal.useCloseBtn = useCloseBtn
  modal.className = params.class || ''
  document.body.appendChild(el)
  modal.show = true
  // 点击遮罩层关闭时触发的事件
  modal.setBeforePressMask(onPressMask)
  modal.setBeforePressClose(onPressClose)
  return modal
}

export { showModal }

export default Modal
