/* eslint-disable max-len */
/* eslint-disable import/prefer-default-export */
import { getMyUserInfo, info } from '@/api'
import { handleApiResult } from '@badam/common-utils'
import store from '.'

export async function getMyInfo() {
  try {
    const res = await getMyUserInfo()
    if (handleApiResult(res)) {
      const [, { data }] = res
      store.state.myInfo = data
    }
  } catch (e) {
    console.log(e)
  }
}

export async function getActInfo() {
  try {
    const res = await info()

    if (handleApiResult(res)) {
      const [, { data }] = res
      store.state.actInfo = data.info

      store.state.start = data.start_ts * 1000
      store.state.end = data.end_ts * 1000
    }
  } catch (e) {
    console.log(e)
  }
}
